var alertErrors = function (msg, url, linenumber) {
  alert('Error message: ' + msg + '\nURL: ' + url + '\nLine Number: ' + linenumber);
  return true;
};
window.onerror = alertErrors;
module.exports = {
  set: function (val){
    if(val)
      window.onerror = alertErrors;
    else
      window.onerror = undefined;
  }
}
