var baseUrls = {
  "dancenow.local": "http://dancenow.local:3005",
  "malcoriel.us.to": "http://malcoriel.us.to:3005",
  "vkuzmin": "http://vkuzmin.kontur:3005",
  "candymaker": "http://162.251.70.58:5943",
  "malc-laptop": "http://192.168.0.3:3005"
};
var gmaps = require('gmaps');
var $ = require('jquery');
global.$ = global.jQuery = $;
require('jquery-mockjax');

var mockApis = require('./mock-apis.js');
var _ = require('underscore');
var baseUrl = baseUrls["candymaker"];
var api;
module.exports = api = {
  getIEvents: function (filter) {
    return $.get(baseUrl + "/api/iEvents", {filter: filter});
  },

  getEvent: function (id) {
    return $.get(baseUrl + "/api/events/" + id);
  },

  getIEvent: function (id) {
    return $.get(baseUrl + "/api/iEvents/" + id);
  },

  getCities: function (filter) {
    return $.get(baseUrl + "/api/cities");
  },

  getMyCity: function () {
    var def = $.Deferred();
    /*
     * 56.832678
     lng

     60.615108
     * */

    $.get(baseUrl + "/api/cities/my", {
      myLocation: {
        lat: 56.832678,
        lng: 60.615108
      }
    })
      .done(function (response) {
        var city = response.city;
        def.resolve(city);
      });
    //gmaps.geolocate({
    //  success: function (position) {
    //    console.log(position);
    //
    //  },
    //  error: function(error){
    //    console.error("Error getting location from gmaps: " + error);
    //  }
    //});
    return def.promise();
  },

  getServerVersion: function () {
    return $.get(baseUrl + "/server/version")
      .pipe(function (response) {
        return response.version;
      })
  },
  toggleMockApi: function (enable) {
    if (enable) {
      _.each(mockApis, function (apiDef) {
        baseUrl = "http://mock";
        var url = baseUrl + apiDef.url;
        $.mockjax({
          url: url,
          responseText: apiDef.responseText
        })
      })
    }
    else {
      $.mockjax.clear();
    }
  },

  changeServer: function (name) {
    if (name == "mock") {
      api._serverName = name;
      api._saveServer();
      this.toggleMockApi(true);
      return;
    }
    if (!baseUrls[name]) {
      console.warn("Unknown server name: " + name);
      return;
    }
    this.toggleMockApi(false);
    baseUrl = baseUrls[name];
    api._serverName = name;
    api._saveServer();
  },

  authenticateByVkCode: function (code, redirectUri) {
    return $.get(baseUrl + "/AuthenticateByVkCode?code=" + code + "&redirectUri=" + redirectUri)
      .pipe(function (token) {
        api.setToken(token);
      })
      .pipe(function(){
        $(window).trigger("apiTokenChanged");
      });
  },

  setToken: function (token) {
    api._token = token;
    api._saveToken();
  },

  _saveToken: function () {
    localStorage.setItem("dancenow_token", JSON.stringify(api._token));
  },

  _saveServer: function () {
    localStorage.setItem("dancenow_api_server", api._serverName);
  },
  _tryLoadToken: function () {
    var token = localStorage.getItem("dancenow_token");
    console.log("Loaded token from localStorage " + token);
    if (token) {
      try {
        api._token = JSON.parse(token);
      }
      catch (e) {
        console.error("Error loading token from localstorage");
      }
    }
  },

  _tryLoadServer: function () {
    var server = localStorage.getItem("dancenow_api_server");
    console.log("Loaded server from localStorage " + server);
    if (server) {
      try {
        api.changeServer(server);
        setTimeout(function () {
          $(window).trigger("apiServerChanged", api._serverName);
        }, 1000); // TODO: utilize an event of debug-panel initialization
      }
      catch (e) {
        console.error("Error loading server from localstorage");
      }
    }
  },

  init: function () {
    $.ajaxSetup({
      cache: false
    });
    api._tryLoadToken();
    api._tryLoadServer();
  },

  getIEventDancers: function (iEventId) {
    return $.get(baseUrl + "/api/iEventDancers/optimizedForUi", {iEventId: iEventId})
      .pipe(function (response) {
        return response.result;
      });
  },

  getIEventDJ: function (iEventId) {
    return $.get(baseUrl + "/api/iEventDjs/getWithPhoto", {iEventId: iEventId})
      .pipe(function (response) {
        return response.result;
      });
  },

  _createOrUpdateEventImpl: function (url, event) {
    return $.ajax({
      type: "PUT",
      url: url,
      contentType: "application/json",
      data: JSON.stringify(event)
    });
  },

  createOrUpdateEvent: function (event) {
    var url = baseUrl + "/api/events/";
    return api._createOrUpdateEventImpl(url, event);
  },

  createOrUpdateIEvent: function (event) {
    var url = baseUrl + "/api/iEvents/";
    return api._createOrUpdateEventImpl(url, event);
  },
  getErrorMessage: function (response) {
    if (response.responseText) {
      try {
        var responseObj = JSON.parse(response.responseText);
        if (responseObj.error && responseObj.error.message)
          return responseObj.error.message;
        else
          return "Неизвестная ошибка";
      }
      catch(e){
        return "Неизвестная ошибка";
      }
    }
    return "Неизвестная ошибка";
  },

  getCurrentDancer: function(){
    return $.get(baseUrl + "/api/dancers/" + api._token.userId);
  },

  getCurrentDancerId: function (){
    return api._token ? api._token.userId : null;
  },

  rsvp: function(iEventId){
    if(!api._token){
      $(document.body).pagecontainer("change","#vk-login");
      return;
    };
    var url = baseUrl + "/api/iEventDancers/";
    var eventDancer = {
      iEventId: iEventId,
      dancerId: api._token.userId
    };
    return $.ajax({
      type: "PUT",
      url: url,
      contentType: "application/json",
      data: JSON.stringify(eventDancer)
    });
  },

  cancelRsvp: function(iEventId){
    if(!api._token){
      $(document.body).pagecontainer("change","#vk-login");
      return;
    };

    var request = {filter: {where:
    {
      iEventId: iEventId,
      dancerId: api._token.userId
    }}};

    return $.get(baseUrl + "/api/iEventDancers/findOne", request)
      .pipe(function(iEventDancer){
        return $.ajax({
          url: baseUrl + "/api/iEventDancers/" + iEventDancer.id,
          type: "DELETE"
        })
      });
  }
};
