var config = require('../../../common/config');
var api = require('./api');
var $ = require('jQuery');


var childWindow;
var auth_phonegap = function (force) {
  var authURL="https://oauth.vk.com/authorize?client_id=" + config.vk.appId + "&scope="+config.vk.permissions+"&redirect_uri=http://oauth.vk.com/blank.html&display=touch&response_type=code";
  childWindow = window.open(encodeURI(authURL), '_blank', 'location=no');
  childWindow.addEventListener('loadstop', auth_event_url);

  function auth_event_url (event) {
    var tmp=(event.url).split("#");
    if (tmp[0]=='https://oauth.vk.com/blank.html' || tmp[0]=='http://oauth.vk.com/blank.html') {
      childWindow.close();
      var code = getURLParameter('code', tmp[1])
      authenticateByCode(code,'http://oauth.vk.com/blank.html&display=touch&response_type=code');
    }
  };
};
function getURLParameter(name, target) {
  return decodeURIComponent((new RegExp('[?|&]?' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(target || location.search)||[,""])[1].replace(/\+/g, '%20'))||null
}

var authenticateByCode = function (code, redirectUri){
  api.authenticateByVkCode(code, redirectUri).done(function(){
    var backUrl = getURLParameter('backUrl') || '#map-page';
    window.location.href = backUrl;
  })
    .fail(function(error){
      var errorMessage = error && error.user ? error.user : "Произошла неизвестная ошибка";
      $('.js-vk-login-error').text(errorMessage);
    });
};


var auth_pc = function () {
  var authURL="https://oauth.vk.com/authorize?client_id=" + config.vk.appId + "&scope="+ config.vk.permissions + "&display=touch&redirect_uri=" + config.vk.getRedirectUrl();
  window.open(encodeURI(authURL), '_blank', 'location=no');
};

var init_auth_pc = function(){
  window.addEventListener("vkLoginSuccess", function(event) {
    var code = event.detail.code;
    authenticateByCode(code, config.vk.getRedirectUrl());
  }, false);
  window.addEventListener("vkLoginError", function(event) {
    var errorMessage =   event && event.detail && event.detail.error_description ? event.detail.error_description : "Произошла неизвестная ошибка";
    $('.js-vk-login-error').text(errorMessage);
  }, false)
};

var init = function() {
  $('.js-vk-login-button').click(function () {
    $('.js-vk-login-error').text('');
    vk.auth();
  });

  if(!window.fromPhonegap)
    init_auth_pc();
};
var vk;
module.exports = vk = {
  init: init,
  auth: function(){
    if(window.fromPhonegap){
      return auth_phonegap();
    }
    else {
      return auth_pc();
    }
  }
};
