var $ = require('jquery');
var api = require('./api');
var cordovaLoader = require('./cordova-loader');
var alertErrors = require('./alert-errors');
var map = require('./map');

var displayClientVersion = function(){
  $(".js-client-version").text(global.version);
};

var displayServerVersion = function(){
  api.getServerVersion().done(function (serverVersion) {
    $(".js-server-version").text(serverVersion);
  })
    .fail(function () {
      $(".js-server-version").text("?");
    })
};

var debugPanel = module.exports = {
  init: function (){
    //$("body > [data-role='panel']").panel().enhanceWithin(); TODO: find how to make a global panel without that shit
    //$('#event').popup();
    //api.toggleMockApi(true);
    $(window).on("apiServerChanged", function(event, name){
      var $apiServer = $("#apiServer");
      $apiServer.find('option').filter(function() {
        return $(this).val() === name;
      }).prop('selected', true);
      $apiServer.selectmenu("refresh", true);
      debugPanel.refreshUserInfo();
    });
    $(window).on("apiTokenChanged", function(event){
      debugPanel.refreshUserInfo();
    });
    displayClientVersion();
    displayServerVersion();
    alertErrors.set(false);
    $(".js-mapinit-button").click(function(){
      map.loadCurrentMap();
    });
    $("#apiServer").change(function () {
      var serverName = $(this).find(":selected").val();
      api.changeServer(serverName);
      map.loadCurrentMap();
      displayServerVersion();
    });
    $("#alertErrorsFlip").change(function () {
      var enable = $(this).find(":selected").val() === "1";
      alertErrors.set(enable);
    });

    $('.js-test-popup').click(function(){
      $('#edit-event-popup').popup('open');
    });

    cordovaLoader.done(function () {
      // strange, but jQuery 'on' does not work here
      document.addEventListener("menubutton", function () {
        $("#debug").panel('toggle');
      }, false);
    });
  },
  refreshUserInfo: function(){
    return api.getCurrentDancer()
      .done(function(dancer){
        $(".js-user-info").show();
        $(".js-user-name").text(dancer.name);
        $(".js-user-photo").css({
          "background-image": "url(" + dancer.photo50Url + ")"
        });
      })
      .fail(function(){
        $(".js-user-info").hide();
      })
  }
};
