// require this script before your document is done loading
var $=require('jquery');
var def = $.Deferred();
window.fromPhonegap = document.URL.indexOf( 'http://' ) === -1 && document.URL.indexOf( 'https://' ) === -1;
if (fromPhonegap) {
  $.getScript("cordova.js").done(function () {
    def.resolve();
  });
} else {
  def.resolve();
}
module.exports = def.promise();
