var api = require("./api");
var _ = require('underscore');
var Mustache = require('mustache');
global.Mustache = Mustache;
var eventPopup = module.exports = {};
eventPopup.show = show;
var EventStatus = require('../../../common/eventStatus.js');
function preprocessEvent(event, dancers) {
  event.time = event.from + "-" + event.to;
  event.status = {};
  event.totalCount = dancers.totalCount;
  switch (event.eventStatus) {
    case EventStatus.NotStarted:
      event.status.notStarted = true;
      event.notCancelled = true;
      event.countText = "Планируют пойти";
      break;
    case EventStatus.Ongoing:
      event.status.ongoing = true;
      event.notCancelled = true;
      event.countText = "Сейчас там";
      break;
    case EventStatus.Cancelled:
      event.status.cancelled = true;
      event.notCancelled = false;
      break;
    default:
      // TODO ?
      break;
  }
  event.guysPresent = dancers.guys.length > 0;
  event.girlsPresent = dancers.girls.length > 0;
}

function show(event) {
  eventPopup.getEventInfo(event).done(function (dancers, dj) {
    var popup = $('.js-event');
    eventPopup.refresh();
    $('.js-event').popup('open');
    popup.enhanceWithin();
  });
  return false;
}

eventPopup.getEventInfo = function (event) {
  $.mobile.loading('show'); // TODO make showDialogsFor
  return $.when(api.getIEventDancers(event.iEventId), api.getIEventDJ(event.iEventId))
    .always(function (dancers, dj) {
      $.mobile.loading('hide');
      eventPopup.currentEvent = event;
      eventPopup.currentDancers = dancers || {};
      eventPopup.currentDj = dj || {};
    });
};

eventPopup.editEvent = function () {
  var event = eventPopup.currentEvent;
  $('#edit-event-popup').popup('close');
  $('#edit-event-popup').one("popupafterclose", function () {
    localStorage.setItem("dancenow_pageparams", JSON.stringify({eventId: event.eventId}));
    $(document.body).pagecontainer("change", "#event-editor-page");
  });
};

eventPopup.editIEvent = function () {
  var event = eventPopup.currentEvent;
  $('#edit-event-popup').popup('close')
  $('#edit-event-popup').one("popupafterclose", function () {
    localStorage.setItem("dancenow_pageparams", JSON.stringify({iEventId: event.iEventId}));
    $(document.body).pagecontainer("change", "#event-editor-page");
  });
};

eventPopup.showEditPopup = function () {
  console.log("showEdit");
  $('.js-event').popup('close');
  $('.js-event').one("popupafterclose", function () {
    $('#edit-event-popup').popup('open');
  });
};

eventPopup.rsvp = function (iEventId) {
  return api.rsvp(iEventId)
    .always(function () {
      eventPopup.updateAndRefresh();
    });
};

eventPopup.cancelRsvp = function (iEventId) {
  return api.cancelRsvp(iEventId)
    .always(function () {
      eventPopup.updateAndRefresh();
    });
};

eventPopup.init = function () {
  $(function () {
    $.get("event.html").done(function (response) {
      $(response).appendTo($("#map-page")).enhanceWithin();
      var editEventPopup = $('.js-edit-event-popup');
      editEventPopup.find(".js-edit-event").on("click", eventPopup.editEvent);
      editEventPopup.find(".js-edit-ievent").on("click", eventPopup.editIEvent);
      editEventPopup.parent().on("focus", function () {
        $(this).blur();
      });
      $('.js-event').parent().on("focus", function () {
        $(this).blur();
      });
    });
  });
};

eventPopup.updateAndRefresh = function(){
  eventPopup.getEventInfo(eventPopup.currentEvent).always(function(){
    eventPopup.refresh();
  })
};

eventPopup.updateRsvpButtons = function() {
  var iAmAttending = false;
  var currentDancerId = api.getCurrentDancerId();
  if(currentDancerId) {
    _.each(eventPopup.currentDancers.guys, function (dancer) {
      console.log("Checking " + JSON.stringify(dancer));
      if (dancer.dancerId === currentDancerId) {
        iAmAttending = true;
        return false;
      }
    });
    _.each(eventPopup.currentDancers.girls, function (dancer) {
      if (dancer.dancerId === currentDancerId) {
        iAmAttending = true;
        return false;
      }
    });
  }

  if(iAmAttending){
    $('.js-event').find('.js-rsvp').hide();
    $('.js-event').find('.js-cancel-rsvp').show();
  }
  else{
    $('.js-event').find('.js-rsvp').show();
    $('.js-event').find('.js-cancel-rsvp').hide();
  }
};

eventPopup.refresh = function () {
  var dancers = eventPopup.currentDancers || {};
  var event = eventPopup.currentEvent || {};
  var dj = eventPopup.currentDj || {};
  var popup = $('.js-event');
  var template = popup.find(".js-template");
  var container = popup.find(".js-container");
  preprocessEvent(event, dancers);
  container.html(Mustache.render(template.html(), _.extend({}, event, dancers, dj)));
  container.find(".js-try-edit-event").on("click", eventPopup.showEditPopup)
  container.find(".js-rsvp").on("click", _.bind(eventPopup.rsvp, null, event.iEventId));
  container.find(".js-cancel-rsvp").on("click", _.bind(eventPopup.cancelRsvp, null, event.iEventId));
  eventPopup.updateRsvpButtons();

};

