var mapEventsOverlay = module.exports = {};
var _ = require('underscore');
mapEventsOverlay.init = function(map, cityLocation){
  // TODO: proper city bounds in DB
  mapEventsOverlay.$initDef = $.Deferred();
  // most of the code is copy-pasted from google's example of USGSOverlay
  var swBound = new google.maps.LatLng(cityLocation.lat - 0.1, cityLocation.lng - 0.1);
  var neBound = new google.maps.LatLng(cityLocation.lat + 0.1, cityLocation.lng + 0.1);
  var bounds = new google.maps.LatLngBounds(swBound, neBound);
  mapEventsOverlay._overlay = new USGSOverlay(bounds, map);
  mapEventsOverlay._overlay.show();
  mapEventsOverlay.markers = [];
  return mapEventsOverlay.$initDef.promise();
};

mapEventsOverlay.updateMarkerImageLocation = function(img, marker){
  var overlayProjection = mapEventsOverlay._overlay.getProjection();
  var sw = overlayProjection.fromLatLngToDivPixel(mapEventsOverlay._overlay.bounds_.getSouthWest());
  var ne = overlayProjection.fromLatLngToDivPixel(mapEventsOverlay._overlay.bounds_.getNorthEast());
  var markerLocation = new google.maps.LatLng(marker.location.lat, marker.location.lng);
  var newCooords =  overlayProjection.fromLatLngToDivPixel(markerLocation);
  img.style.left = (Math.abs(newCooords.x - sw.x) - 36) + "px";
  img.style.top = (Math.abs(newCooords.y - ne.y) - 72) + "px";
};

mapEventsOverlay.addEventMarker = function(marker){
  var div = mapEventsOverlay._overlay.div_;
  var img = document.createElement('img');
  img.src = marker.icon;
  img.className = 'event-marker';
   div.appendChild(img);
  mapEventsOverlay.markers.push({img: img, marker: marker});
  mapEventsOverlay.updateMarkerImageLocation(img, marker);
  /*
   lat: iEvent.location.lat,
   lng: iEvent.location.lng,
   icon: icon,
   title: iEvent.title,
   click: _.partial(eventPopup.show, iEvent)
  * */
};

  var overlay;

  USGSOverlay.prototype = new google.maps.OverlayView();

  /** @constructor */
  function USGSOverlay(bounds, map) {

    // Now initialize all properties.
    this.bounds_ = bounds;
    this.map_ = map;

    // Define a property to hold the image's div. We'll
    // actually create this div upon receipt of the onAdd()
    // method so we'll leave it null for now.
    this.div_ = null;

    // Explicitly call setMap on this overlay
    this.setMap(map);
  }

  /**
   * onAdd is called when the map's panes are ready and the overlay has been
   * added to the map.
   */
  USGSOverlay.prototype.onAdd = function() {

    var div = document.createElement('div');
    div.style.border = 'none';
    div.style.borderWidth = '0px';
    div.style.position = 'absolute';

    // Create the img element and attach it to the div.
    //var img = document.createElement('img');
    //img.src = this.image_;
    //img.style.width = '100%';
    //img.style.height = '100%';
    //div.appendChild(img);

    this.div_ = div;

    // Add the element to the "overlayImage" pane.
    var panes = this.getPanes();
    panes.overlayImage.appendChild(this.div_);
    mapEventsOverlay.$initDef.resolve();
  };

  USGSOverlay.prototype.draw = function() {

    // We use the south-west and north-east
    // coordinates of the overlay to peg it to the correct position and size.
    // To do this, we need to retrieve the projection from the overlay.
    var overlayProjection = this.getProjection();

    // Retrieve the south-west and north-east coordinates of this overlay
    // in LatLngs and convert them to pixel coordinates.
    // We'll use these coordinates to resize the div.
    var sw = overlayProjection.fromLatLngToDivPixel(this.bounds_.getSouthWest());
    var ne = overlayProjection.fromLatLngToDivPixel(this.bounds_.getNorthEast());

    // Resize the image's div to fit the indicated dimensions.
    var div = this.div_;
    div.style.left = sw.x + 'px';
    div.style.top = ne.y + 'px';
    div.style.width = (ne.x - sw.x) + 'px';
    div.style.height = (sw.y - ne.y) + 'px';

    _.each(mapEventsOverlay.markers, function(pair){
      var img = pair.img;
      var marker = pair.marker;
      mapEventsOverlay.updateMarkerImageLocation(img, marker);
    });
  };

  USGSOverlay.prototype.onRemove = function() {
    this.div_.parentNode.removeChild(this.div_);
  };

// Set the visibility to 'hidden' or 'visible'.
  USGSOverlay.prototype.hide = function() {
    if (this.div_) {
      // The visibility property must be a string enclosed in quotes.
      this.div_.style.visibility = 'hidden';
    }
  };

  USGSOverlay.prototype.show = function() {
    if (this.div_) {
      this.div_.style.visibility = 'visible';
    }
  };

  USGSOverlay.prototype.toggle = function() {
    if (this.div_) {
      if (this.div_.style.visibility == 'hidden') {
        this.show();
      } else {
        this.hide();
      }
    }
  };

// Detach the map from the DOM via toggleDOM().
// Note that if we later reattach the map, it will be visible again,
// because the containing <div> is recreated in the overlay's onAdd() method.
  USGSOverlay.prototype.toggleDOM = function() {
    if (this.getMap()) {
      // Note: setMap(null) calls OverlayView.onRemove()
      this.setMap(null);
    } else {
      this.setMap(this.map_);
    }
  };
