var api = require('./api');
var GMaps = require('gmaps');
var _ = require('underscore');
var Mustache = require('mustache');
global.Mustache = Mustache;
var eventPopup = require('./event-popup');
var EventStatus = require('../../../common/eventStatus.js');
var mapEventsOverlay = require('./map-events-overlay');
var initialized = false;
var gmap;
var mockMap = false;

var map = module.exports = {
  loadCurrentMap: function () {
    if (!GMaps) {
      mockMap = true;
    }
    return api.getMyCity()
      .pipe(function (city) {
        if (!city) {
          return;
        }
        if(!mockMap)
          gmap = new GMaps({
            div: '#map',
            lat: city.location.lat,
            lng: city.location.lng,
            width: "100%",
            height: "height: 100%",
            zoom: city.zoom,
            panControl: false,
            zoomControl: true,
            mapTypeControl: false,
            scaleControl: false,
            streetViewControl: false,
            overviewMapControl: false
          });
        else{
          // TODO mock map
        }
        $(".js-cityname").text(city.name);
        return map.loadEvents(city.cityId)
          .pipe(function(iEvents){
            return {city: city, iEvents: iEvents};
          });
      })
  },

  loadEvents: function (cityId) {
    return api.getIEvents({where: {cityId: cityId}});
  },

  tryLoadCurrentMap: function (opts) {
    // opts.toPage.attr("id") === "map-page" &&
    if (!initialized) {
      map.loadCurrentMap()
        .pipe(function (cityAndEvents) {
          var city = cityAndEvents.city;
          var iEvents = cityAndEvents.iEvents;
          initialized = true;
          if(mockMap)
            map.initMockMapEvents(city, iEvents);
          else
            map.initMapEvents(city, iEvents);

        });
    }
  },

  init: function () {
    map.tryLoadCurrentMap();
    eventPopup.init();
  }
};

map.initMockMap = function(city, iEvents){
  $("#map").html('');
  _.each(iEvents, function(iEvent){
    var marker = $("<div/>").text("lat: " + iEvent.location.lat + " lng: " + iEvent.location.lng + " title: " + iEvent.title).css({"border" : "solid black 1px"});
    marker.click(_.partial(eventPopup.show, iEvent));
    $("#map").append(marker);
  })
};

map.initMapEvents = function(city, iEvents){
  mapEventsOverlay.init(gmap.map, city.location)
    .done(function(){
      _.each(iEvents, function (iEvent) {
        var icon;
        switch (iEvent.eventStatus) {
          case EventStatus.NotStarted:
            icon = "dist/images/pin-azure-72.png";
            break;
          case EventStatus.Ongoing:
            icon = "dist/images/pin-green-72.png";
            break;
          case EventStatus.Cancelled:
            icon = "dist/images/pin-pink-72.png";
            break;
          default:
            icon = "dist/images/pin-azure-72.png";
            break;
        }
        mapEventsOverlay.addEventMarker({
          icon: icon,
          location: iEvent.location,
          click: _.partial(eventPopup.show, iEvent),
          title: iEvent.title
        });
      });
    });

};

