require('jquery-mobile');
var shortid = require('shortid');
var eventEditor = {};
var api = require('./api')
var getURLParameter = require('./get-url-parameter');
var moment = require('moment');
var EventStatus = require('./../../../common/eventStatus.js');
var map = require('./map');

eventEditor.submit = function () {
  var page = $("#event-editor-page");
  var event = {
    "what": page.find(".js-what").val(),
    "where": page.find(".js-where").val(),
    "eventId": isNewEvent ? shortid.generate() : page.find(".js-eventId").val(),
    "iEventId": isEventInstance && isNewEvent ? shortid.generate() : page.find(".js-iEventId").val(),
    "date": isEventInstance && isNewEvent ? moment().clone().toDate() : page.find(".js-date").val(),
    "eventStatus": isEventInstance && isNewEvent ? EventStatus.NotStarted : page.find(".js-event-status").val(),
    "from": page.find(".js-from").val(),
    "when": page.find(".js-when").val(),
    "title": page.find(".js-title").val(),
    "location": page.find(".js-location").val(),
    "cityId": "1"
  };
  var $action;
  if (isEventInstance)
    $action = api.createOrUpdateIEvent(event);
  else
    $action = api.createOrUpdateEvent(event);
  $("#event-editor-page").find('.js-errors').text('');
  $action.done(function(){
    map.loadCurrentMap();
    $(document.body).pagecontainer("change","#map-page");
  })
    .fail(function(response){
      $("#event-editor-page").find('.js-errors').text(api.getErrorMessage(response));
    })
};;

var mockEvent = {
  "what": "Бачата",
  "where": "Планетарий",
  "from": "21:30",
  "when": "each Sat",
  "title": "Бачата пати",
  "location": "56.827581,60.581601"
}

eventEditor.fill = function (event) {
  if (typeof(event.location) === 'object')
    event.location = event.location.lat + ',' + event.location.lng
  var page = $("#event-editor-page");
  page.find(".js-what").val(event.what);
  page.find(".js-eventId").val(event.eventId);
  page.find(".js-iEventId").val(event.iEventId);
  page.find(".js-date").val(event.date);
  page.find(".js-event-status").val(event.eventStatus);
  page.find(".js-where").val(event.where);
  page.find(".js-from").val(event.from);
  page.find(".js-when").val(event.when);
  page.find(".js-title").val(event.title);
  page.find(".js-location").val(event.location);
};

eventEditor.fillMock = function () {
  eventEditor.fill(mockEvent);
};

var isNewEvent = true;
var isEventInstance = false;
eventEditor.initPageControls = function () {
  $("#event-editor-page").find(".js-submit").click(eventEditor.submit);
  $("#event-editor-page").find(".js-fill-mock").click(eventEditor.fillMock);
  $(document.body).on("pagecontainerbeforeshow", function (event, ui) {
    if (ui.toPage && ui.toPage.attr("id") !== "event-editor-page")
      return;
    try {
      var pageParams = JSON.parse(localStorage.getItem("dancenow_pageparams") || "{}");
      var eventId = pageParams.eventId;
      var iEventId = pageParams.iEventId;
    }
    catch(e){
      console.error("Failed to get page params " + e);
    }
    console.log("editing eventId: " + eventId + " , iEventId: " + iEventId);
    localStorage.setItem("dancenow_pageparams","");
    var page = $("#event-editor-page");
    if (eventId) {
      isNewEvent = false;
      isEventInstance = false;
      page.find('.js-header').text("Редактирование всех вечеринок");
      api.getEvent(eventId)
        .pipe(function (event) {
          page.find('.js-header').text("Редактирование всех вечеринок " + event.title);
          page.find('.js-submit').text("Отредактировать");
          eventEditor.fill(event);
        });
    }
    else if (iEventId) {
      isNewEvent = false;
      isEventInstance = true;
      page.find('.js-title').text("Редактирование вечеринки");
      api.getIEvent(iEventId)
        .pipe(function (event) {
          page.find('.js-header').text("Редактирование вечеринки " + event.title);
          page.find('.js-submit').text("Отредактировать");
          eventEditor.fill(event);
        });
    }
    else {
      console.log("new event");
      isNewEvent = true;
      isEventInstance = false;
      page.find('.js-header').text("Новая вечеринка");
      page.find('.js-submit').text("Создать");
      eventEditor.fill({});
    }
  });
};
eventEditor.init = function () {
  $.get("event-editor.html").done(function (response) {
    $(response).appendTo($("#event-editor-page")).enhanceWithin();
    eventEditor.initPageControls();
  });
};

module.exports = eventEditor;
