require('./alert-errors');
require('./browser-sync-redirect').init();
require('./jquery-url-fixer').init();
var $ = require('jquery');1
var $ = global.$ = global.jQuery = $;
var api = require('./api');
var _ = require('underscore');
require("./../../../version");
var vk = require('./vk-login');

$.mobile = require('jquery-mobile');

//require('./external/jquery.mobile.dynamic.popup');
var map = require('./map');
var debugPanel = require('./debug-panel');
var eventEditor = require('./event-editor');
$(document).one("pagecreate",function () {
  if(document.location.hash === "#" || !document.location.hash)
    $(document.body).pagecontainer('change','#map-page');
  api.init();
  map.init();
  $.get("debug-panel.html").done(function(response){
    // TODO: it's pretty strange that debug panel is in the map. I got to make it global panel (but don't know how right now)
    $(response).appendTo($("#map-page")).enhanceWithin();
    debugPanel.init();
  });
  vk.init();
  eventEditor.init();
});
